import React, { useContext, useState, useEffect } from 'react'
import './StampList.css'
import arrow from '../../../assets/arrow@2x.png'
import more from '../../../assets/More.svg'
import invoke from '../../../utils/invoke'
import { MakeContext } from '../../MakeContext'
import { LocaleContext } from '../../../contexts/LocaleContext'
import { throttle, InitializationThrottle } from '../../../utils/throttle'
import AlertModal from '../../../components/AlertModal'
function StampList (props) {
  const make = useContext(MakeContext)
  const [alertModelVisible, setAlertModelVisible] = useState(false)
  let district = useContext(LocaleContext)
  const [layerTypes, setLayerTypes] = useState([])
  // const [stampId, setStampId] = useState([])
  const {
    layers
  } = make
  const { data = [], _index, showModal = () => {}, type } = props
  let imgs
  let over = 'NG'
  let isModalOver = 'NG'
  let layersArr = layers
  // let layerTypes = []
  let isNew = false

  useEffect(() => {
    InitializationThrottle()
    invoke('log', 'stampListlayers5' + JSON.stringify(layers))
    let arr = []
    // let layerStampId = []
    layers.forEach((item) => {
      arr.push(item.type)
      // if(item.stampRule){
      //   layerStampId.push(item.stampRule.stampId)
      // }
    })
    // setStampId(layerStampId)
    setLayerTypes(arr)
  }, [])
  if (layersArr[layersArr.length - 1]) {
    delete layersArr[layersArr.length - 1].stampRule
  }
  if (layersArr[0]) {
    layersArr.forEach(element => {
      if (element.stampRule) {
        if (element.stampRule.stampFlag.flag_can_other_layer_over === 'NG') {
          over = 'OK'
        }
      }
    })
  }
  if (data[0]) {
    const preFourData = data.slice(0, 4)
    imgs = preFourData.map((d, index) => {
      let disabled = false
      if (d.flag_can_other_layer_over === 'NG' && over === 'OK') {
        isModalOver = 'OK'
        disabled = true
      }
      // if ((d.flag_layer_stamp === 'NG' && layers.length > 1) || (d.flag_layer_paint === 'NG' && layers.length > 1) || (d.flag_layer_text === 'NG' && layers.length > 1) || (d.flag_layer_photo === 'NG' && layers.length > 1)) {
      //   isModalOver = 'NG'
      //   disabled = true
      // }
      if (layers.length > 1) {
        if ((d.flag_layer_stamp === 'NG' && layerTypes.indexOf('stamp') > -1) || (d.flag_layer_paint === 'NG' && layerTypes.indexOf('paint') > -1) || (d.flag_layer_text === 'NG' && layerTypes.indexOf('text') > -1) || (d.flag_layer_photo === 'NG' && layerTypes.indexOf('photo') > -1)) {
          isModalOver = 'NG'
          disabled = true
        }
      }
      if (d.is_new) {
        isNew = true
      }
      if (index < 4) {
        return (
          <div className='imgWrap' key={`c${_index}${index}`}
            style={{ filter: `grayscale(${disabled ? '100%' : '0%'})` }}
          >
            <img src={d.main_image_url} alt='left' />
          </div>
        )
      }
    })
  }
  let singleStamp:any
  let singleStampOther:any

  async function isShowModal (d, number, disabled) {
    if (disabled) {
      let alertModal = []
      let LayerTypesString = JSON.stringify(layerTypes)
      let newLayerTypes = JSON.parse(LayerTypesString)
      newLayerTypes.pop()
      try {
        newLayerTypes.forEach((ele) => {
          if (d[`flag_layer_${ele}`] === 'NG') {
            alertModal.push(ele)
          }
        })
        if (alertModal.length > 0) {
          await invoke('stampDisabled', alertModal)
        } else {
          await invoke('stampDisabledOver')
        }
      } catch (error) {
        setAlertModelVisible(true)
        newLayerTypes.forEach((ele) => {
          if (d[`flag_layer_${ele}`] === 'NG') {
            alertModal.push(ele)
          }
        })
        if (alertModal.length > 0) {
          await invoke('stampDisabled', alertModal)
        } else {
          await invoke('stampDisabledOver')
        }
      }
    } else {
      showModal(d, number)
    }
  }

  if (data[0]) {
    singleStampOther = data.map((d, index) => {
      let disabled = false

      if (d.flag_can_other_layer_over === 'NG' && over === 'OK') {
        disabled = true
      }
      if (layers.length > 1) {
        if ((d.flag_layer_stamp === 'NG' && layerTypes.indexOf('stamp') > -1) || (d.flag_layer_paint === 'NG' && layerTypes.indexOf('paint') > -1) || (d.flag_layer_text === 'NG' && layerTypes.indexOf('text') > -1) || (d.flag_layer_photo === 'NG' && layerTypes.indexOf('photo') > -1)) {
          disabled = true
        }
      }

      return <div key={`z${index}`} className='singleStampWrap' onClick={() => { isShowModal(d, 1, disabled) }} style={{ filter: `grayscale(${disabled ? '100%' : '0%'})` }}>
        <div className='leftContent'>
          <img src={d.main_image_url} alt='left' />
        </div>
        <div className='middleContent'>
          <div>
            <div className={district.current === 'en' ? 'holder' : 'holderJan'}>{d.contents_holder_name}</div>
            <div style={{ display: 'flex', alignItmes: 'center', flex: 1, justifyContent: 'flex-start' }}>
              {d.is_new && <div className={district.current === 'en' ? 'new' : 'newJAn'}>
                <img src={require('../../../assets/New.svg')} alt='' style={{ width: 24, height: 24 }} />
              </div>
              }
              {district.current === 'en' ? <div className='name' style={{ fontSize: 17, color: disabled ? '#666' : '' }}>{d.name}</div> : <div className='nameJan' style={{ fontSize: 16, color: disabled ? '#666' : '' }}>{d.name}</div>}
            </div>
            <div>{district.current === 'en' ? (d.price === '¥0' ? <div className='price'>Sticker Print Fee: Free</div> : <div className='price' style={{ width: window.innerWidth - 108 }}>Sticker Print Fee: {d.price}+Tax / 1 Item</div>)
              : (d.price === '¥0' ? <div className='priceJan'>スタンププリント代：無料</div> : <div className='priceJan' style={{ width: window.innerWidth - 108 }}> スタンププリント代：{d.price}+ 消費税 / 1 アイテム</div>)}</div>
          </div>
        </div>
        <div className='rightContent'>
          <img src={arrow} alt='right' />
        </div>
      </div>
    }
    )
  }

  singleStamp = (
    <div>
      {singleStampOther}
    </div>
  )
  const multiStamp = (
    <div className='mulStampWrap'
      onClick={() => { throttle(() => isShowModal(data, 2, false)) }}

    >
      {district.current === 'en'
        ? <div className='top'>
          <span style={{ display: 'inline-block', marginRight: isNew ? 4 : 0, height: '100%', display: 'flex', alignItems: 'center' }}>{isNew && <img src={require('../../../assets/New.svg')} alt='' style={{ width: 30, height: 16 }} />}</span>
          <span style={{ display: 'inline-block', height: '100%', display: 'flex', alignItems: 'center' }}>{data[0] && data[0].contents_holder_name}</span>
        </div>
        : <div className='topJan'>
          <span style={{ display: 'inline-block', marginRight: isNew ? 4 : 0, height: '100%', display: 'flex', alignItems: 'center' }}>{isNew && <img src={require('../../../assets/New.svg')} alt='' style={{ width: 30, height: 16 }} />}</span>
          <span style={{ display: 'inline-block', height: '100%', display: 'flex', alignItems: 'center' }}>{data[0] && data[0].contents_holder_name}</span>
        </div>
      }
      <div className='bottom'>
        <div className='leftContent'>
          {imgs}
        </div>
        <div className='middleContent'>
          {imgs && imgs.length > 3 && <img src={more} alt='' />}
        </div>
        <div className='rightContent'>
          <img src={arrow} alt='' />
        </div>
      </div>
    </div>)

  let result:any
  if (type === 2) {
    result = multiStamp
  } else if (type === 1 || type === 3) {
    result = singleStamp
  }
  return (
    <div>
      {result}
      <AlertModal visible={alertModelVisible} title='error.network.title' description='error.network.description' text='OK' onClick={() => { setAlertModelVisible(false) }} />
    </div>
  )
}
export default StampList
