import invoke from './invoke'

async function checkInstruction (key) {
  let result = await invoke('getStorageItem', { key })
  return result === '1'
}

async function setInstructionVisited (key) {
  await invoke('setStorageItem', { key, value: '1' })
}

async function checkGlobalInstruction (key) {
  let result = await invoke('getGlobal', { key })
  return result === '1'
}

async function setGlobalInstructionVisited (key) {
  await invoke('setGlobal', { key, value: '1' })
}

export default {
  checkInstruction,
  setInstructionVisited,
  checkGlobalInstruction,
  setGlobalInstructionVisited
}
