import React, { useContext, useEffect, useState } from 'react'
import { MakeContext } from '../MakeContext'
import StampList from './StampList'
import Navbar from '../navbar'
import StampModal from './StampModal'
import invoke from '../../utils/invoke'
import urls from '../../utils/urls'
import './stamp.css'
import attention from '../../assets/attention.png'
import arrow from '../../assets/arrow@2x.png'
import IsAgainEdit from '../../components/isAgainEdit'
import { LocaleContext } from '../../contexts/LocaleContext'
import { throttle, InitializationThrottle } from '../../utils/throttle'
import Locale from '../../components/Locale'
import instruction from '../../utils/instruction'
import constants from '../../constants/instruction'
import AlertModal from '../../components/AlertModal'
import code from '../../utils/code'
import useRouter from '../../hooks/useRouter'
var moreNum = 0
var singleNum = 0
var recentNum = 0
var moreStamp = []
var singleSta = []
var recentSta = []
export default function Stamp (props) {
  const make = useContext(MakeContext)
  const languageType = useContext(LocaleContext)
  const [getRecent, setGetRecent] = useState([])
  const [isAgainEdit, setIsAgainEdit] = useState(false)
  const [firstVisited, setFirstVisited] = useState('')
  const [alertModelVisible, setAlertModelVisible] = useState(false)
  const {
    changeContentsHolderIds,
    moreStampArr,
    changeMoreStampArr,
    singleStampArr,
    changeSingleStampArr,
    layers,
    previousPage
    // countryCode
  } = make

  const district = useContext(LocaleContext)
  const [loading, setLoading] = useState(true)
  const router = useRouter()
  const passProps = code.decodeURI(router.query.passProps || '{}')
  async function showModal (stamp, type) {
    try {
      const countryCode = await invoke('getCountryCode')
      if (type === 1) {
        make.changePreStamp(stamp)
        make.changeSelectedStamp(stamp)
        make.setModalVisible(true)
      } else if (type === 2) {
        if (layers.length > 0) {
          delete layers[layers.length - 1].stampRule
        }
        await invoke('setLayers', layers)
        invoke('navigateTo', {
          to: '/make/stamp/stampShowcase',
          params: {
            countryCode: countryCode,
            stamp: stamp.map(item => {
              return item
            }),
            previousPage: previousPage
          }
        })
      }
    } catch (e) {
      setAlertModelVisible(true)
    }
  }

  async function getStamps () {
    try {
      const countryCode = await invoke('getCountryCode')
      let countryCode1 = await invoke('getStorageItem', { key: 'countryCode' })
      let stampSet = await invoke('getStampList')
      if (JSON.stringify(stampSet) !== '{}' && countryCode1 === countryCode) {
        getRecentFu('unload', stampSet, countryCode)
      } else {
        await invoke('setStorageItem', { key: 'countryCode', value: countryCode })
        let STAMPSETUrl
        if (district.current === 'ja') {
          STAMPSETUrl = `${urls.URL_STAMPSET_LIST}?lang=ja`
        } else {
          STAMPSETUrl = `${urls.URL_STAMPSET_LIST}?lang=%s`
        }
        stampSet = await invoke('request', { url: STAMPSETUrl })
        getRecentFu('reload', stampSet, countryCode)
      }
      const stampIds = stampSet.stamp_sets.map(a => a.id)
      const stampIdArr = Array.from(new Set(stampIds)).sort((a, b) => a - b)
      let newStampSets = []
      stampIdArr.map(s => {
        stampSet.stamp_sets.map(st => {
          if (st.id === s) {
            newStampSets.push(st)
          }
        })
      })
      make.changeStampSet(newStampSets)
      const contentsHolderIds = stampSet.stamp_sets.map(a => a.contents_holder_id)
      const ids = Array.from(new Set(contentsHolderIds)).sort((a, b) => a - b)
      changeContentsHolderIds(ids)
      let moreStampArr = []
      let singleStampArr = []
      ids.forEach(i => {
        const stamp = stampSet.stamp_sets.filter(s => s.contents_holder_id === i)
        const l = stamp.length
        if (l > 1) {
          moreStampArr.push(stamp)
        } else {
          const isUniqlo = stamp[0].contents_holder_name.toLowerCase() !== 'uniqlo'
          if (isUniqlo) {
            singleStampArr.push(stamp)
          } else {
            make.changeUniqloStamp(stamp)
          }
        }
      })
      changeMoreStampArr(moreStampArr)
      changeSingleStampArr(singleStampArr)
      setLoading(false)
    } catch (e) {
      setAlertModelVisible(true)
    }
  }
  function openWin () {
    invoke('openURL', 'https://utme.uniqlo.com/stamp.php')
  }
  async function getRecentFu (status, data, countryCode) {
    try {
      await invoke('setStampList', data)
      let getRecent = await invoke('getStorageItem', { key: 'recentStampObj' })
      if (JSON.stringify(getRecent) === '{}' || JSON.stringify(getRecent) === '[]') {
        setGetRecent([])
      } else {
        if (status === 'unload') {
          let o = JSON.parse(getRecent)
          let recentStampArr = o[countryCode] ? o[countryCode] : []
          setGetRecent(recentStampArr)
        } else {
          let o = JSON.parse(getRecent)
          let recentStampArr = o[countryCode] ? o[countryCode] : []
          let arr = []

          recentStampArr.forEach((ele, index) => {
            data.stamp_sets.forEach((item, i) => {
              if (item.id === ele.id) {
                arr.push(item)
              }
            })
          })
          o[countryCode] = arr
          await invoke('setStorageItem', { key: 'recentStampObj', value: JSON.stringify(o) })
          setGetRecent(arr)
        }
      }
    } catch (error) {

    }
  }

  async function fixUseEffectWarn () {
    try {
      let visited = await instruction.checkInstruction(constants.Stamp_Page_Instruction)
      if (!visited) {
        setFirstVisited('Y')
        await instruction.setInstructionVisited(constants.Stamp_Page_Instruction)
        setTimeout(() => {
          setFirstVisited('N')
        }, 3000)
      } else {
        setFirstVisited('N')
      }
    } catch (error) {

    }

    getStamps()
  }

  useEffect(() => {
    fixUseEffectWarn()
    InitializationThrottle()
    changeMoreStampArr([])
    changeSingleStampArr([])
    clearState()
    return () => {
      clearState()
    }
  }, [])
  function clearState () {
    moreNum = 0
    singleNum = 0
    recentNum = 0
    moreStamp = []
    singleSta = []
    recentSta = []
  }
  const extraDom = (
    <div className='extraDomWrap' onClick={() => openWin()}>
      <div className='leftImg'>
        <img src={attention} alt='left' />
      </div>
      {district.current !== 'en' ? <div className='introduce'>
      スタンプの使い方、プリント代について
      </div> : <div className='introduceEng'>
      Information on Stickers and Print Fees
      </div>}

      <div className='rightImg' style={{ marginLeft: 7 }}>
        <img src={arrow} alt='right' />
      </div>
    </div>
  )

  const stampListProps = {
    showModal: showModal
  }
  if (moreNum === 0) {
    if (moreStampArr.length > 0) {
      moreNum++
      moreStamp = moreStampArr.map((m, index) => {
        return (
          <div key={`a${index}`} >
            <StampList type={2} data={m} _index={1} {...stampListProps} />
          </div>
        )
      })
    }
  }

  if (singleNum === 0) {
    if (singleStampArr.length > 0) {
      singleNum++
      singleSta = singleStampArr.map((m, index) => {
        return (
          <div key={`b${index}`}>
            <StampList type={1} data={m} _index={2} {...stampListProps} />
          </div>
        )
      })
    }
  }
  if (recentNum === 0) {
    if (getRecent.length > 0) {
      recentNum++
      recentSta = getRecent.map((m, index) => {
        return (
          <div key={`d${index}`}>
            <StampList type={3} data={[m]} _index={3} {...stampListProps} />
          </div>
        )
      })
    }
  }

  const uniqloDom = make.uniqloStamp.map((d, index) =>
    (
      <div key={index} onClick={() => throttle(() => showModal(d, 1))} className='singleStampWrap'>
        <div className='leftContent'>
          <img src={d.main_image_url} alt='left' />
        </div>
        <div className='middleContent'>
          <div style={{ color: '#7d7d7d' }}>
            {district.current === 'en'
              ? <div style={{ fontSize: 13,
                height: 15,
                lineHeight: '15px',
                color: '#7d7d7d',
                overflow: 'hidden',
                textOverflow: 'ellipsis',
                whiteSpace: 'nowrap',
                fontFamily: ['Roboto', 'Helvetica Neue', 'NotoSansCJKjp-Regular'] }}>{d.contents_holder_name}</div>
              : <div style={{ fontSize: 11,
                height: 17,
                lineHeight: '17px',
                color: '#7d7d7d',
                overflow: 'hidden',
                textOverflow: 'ellipsis',
                whiteSpace: 'nowrap',
                fontFamily: ['Roboto', 'Helvetica Neue', 'NotoSansCJKjp-Regular'] }}>{d.contents_holder_name}</div>}
          </div>
          <div style={{
            fontSize: 16,
            color: '#1b1b1b',
            fontFamily: ['Roboto', 'HelveticaNeue-Bold', 'NotoSansCJKjp-Bold']
          }} ><div style={{ display: 'inline-block',
              marginTop: district.current === 'en' ? 4 : 3,
              lineHeight: district.current === 'en' ? '21px' : '19px',
              height: district.current === 'en' ? 21 : 19,
              overflow: 'hidden',
              textOverflow: 'ellipsis',
              whiteSpace: 'nowrap' }}>{d.name}</div></div>
        </div>
        <div style={{ marginTop: 4, position: 'absolute', left: 92, bottom: 10 }}>{district.current === 'en' ? (d.price === '¥0' ? <div style={{ width: '100%',
          fontSize: 13,
          color: '#7D7D7D',
          height: 16,
          lineHeight: '16px',
          textAlign: 'left' }}>Sticker Print Fee: Free</div> : <div style={{ width: '100%',
          fontSize: 13,
          height: 16,
          lineHeight: '16px',
          width: window.innerWidth - 108,
          overflow: 'hidden',
          textOverflow: 'ellipsis',
          whiteSpace: 'nowrap',
          color: '#7D7D7D',
          textAlign: 'left' }}>Sticker Print Fee: {d.price}+Tax / 1 Item</div>)

          : (d.price === '¥0' ? <div style={{ fontFamily: ['Roboto', 'Helvetica Neue', 'NotoSansCJKjp'],
            fontSize: 11,
            lineHeight: '11px',
            height: 17,
            lineHeight: '17px',
            display: 'flex',
            alignItems: 'center',
            marginTop: 4,
            color: '#7D7D7D' }}>スタンププリント代：無料</div> : <div style={{ width: window.innerWidth - 108,
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            fontSize: 11,
            marginTop: 4,
            height: 17,
            color: '#7D7D7D',
            lineHeight: '17px',
            whiteSpace: 'nowrap' }}> スタンププリント代：{d.price} +消費税 / 1 アイテム</div>)}</div>
        <div className='rightContent'>
          <img src={arrow} alt='right' />
        </div>
      </div>
    )
  )
  function change () {
    setIsAgainEdit(true)
  }
  function isAgainFu (e) {
    setIsAgainEdit(false)
  }
  const stampProps:Object = {
    modalVisible: make.modalVisible,
    stamp: make.selectedStamp
  }
  return (
    <div style={{ height: window.innerHeight - 50, paddingBottom: 50 }} className={isAgainEdit || make.modalVisible ? 'isAgainEditClassname' : 'modalVisibleClassname'}>
      <div style={{ display: firstVisited === 'N' ? 'block' : 'none' }} className='firstVisitedClassname'>
        <div className='navBarWrap'>
          <Navbar currentPage='mainPage' page='stamp' revoke={() => { change() }} />
        </div>
        {district.current !== 'en' ? <div className='stampTitle'>
        はじめにお読みください
        </div> : <div className='stampTitleEng'>
        Please read before use
        </div>}
        {extraDom}
        {recentSta.length > 0 && (district.current === 'en' ? <div className='stampTitleEng'>
        Recent Sticker Sets
        </div> : <div className='stampTitle'>
          最近つかったスタンプセット
        </div>)}
        {recentSta}
        {district.current === 'en' ? <div className='stampTitleEng'>
        Browse Sticker Sets
        </div> : <div className='stampTitle'>
        スタンプセット一覧
        </div>}
        <div style={{ overflow: 'hidden' }}>
          <div style={{ marginBottom: '5px' }} >
            {moreStamp}
          </div>
          <div className='firstsingleStamp'>{singleSta}</div>
          <div >{uniqloDom}</div>
          <div className='stampLoading' style={{ display: loading ? 'block' : 'none' }}>
            <img src={require('../../assets/stampLoad.gif')} width={'50px'} />
          </div>
        </div>
        {make.modalVisible ? <StampModal {...stampProps} /> : ''}
        {isAgainEdit ? <IsAgainEdit passProps={passProps} page='stamp' status={isAgainEdit} isAgainFu={isAgainFu} /> : ''}
      </div>
      <div onClick={() => { setFirstVisited('N') }} style={{
        display: firstVisited === 'Y' ? 'flex' : 'none',
        position: 'fixed',
        left: 0,
        right: 0,
        bottom: 0,
        top: 0,
        overflow: 'hidden',
        backgroundColor: '#fff',
        textAlign: 'center',
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column',
        zIndex: 10
      }}>
        <img width={278} height={250.6} src={require('../../assets/Stamp_anime.gif')} />
        <div style={{
          marginTop: 54.4,
          width: '75%',
          color: '#1b1b1b',
          fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
          fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
          fontSize: languageType.current === 'ja' ? '17px' : '20px'
        }}>
          <Locale code='make.home.stamp.instruction' />
        </div>
      </div>
      {alertModelVisible ? <AlertModal visible={alertModelVisible} title='error.network.title' description='error.network.description' text='OK' onClick={() => { 
        setAlertModelVisible(false)
        invoke('goBack')
      }} /> : ''}
    </div>
  )
}
